.menuContainer {
    display: flex;
    justify-content: center;
    align-items: center;
  }
  
  .menu {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    background-color: var(--containerBG);
    color: var(--menuFont);
    border: solid 3px var(--border);
    border-radius: 10px;
    padding: 1rem;
    box-shadow: var(--shadow);
  }
  
  .menuItem {
    margin: 1rem auto;
    box-shadow: var(--buttonShadow);
  }
  
  .customSelect {
    position: relative;
    display: inline-block;
    width: 40px;
  }
  
  .selectedOption {
    display: flex;
    align-items: center;
    padding: 5px;
    cursor: pointer;
    background: var(--containerBG);
    width: 100%;
  }
  
  .options {
    display: none;
    position: absolute;
    top: 100%;
    left: 0;
    width: 100%;
    border: 1px solid var(--border);
    background: var(--containerBG);
  }
  
  .option {
    display: flex;
    align-items: center;
    padding: 5px;
    cursor: pointer;
  }
  
  .option:hover {
    background: #f0f0f0;
  }
  
  .customSelect:hover .options {
    display: block;
  }
  
  button {
    padding: 5px;
    margin: 1rem auto;
    align-items: center;
    background-color: var(--menuFont);
    color: white;
    border: solid 2px var(--border);
    border-radius: 10px;
    box-shadow: var(--buttonShadow);
  }
  
  option,
  select {
    padding: 1rem;
    font-size: 16px;
    background-color: var(--containerBG);
    color: var(--menuFont);
  }